import * as React from 'react';
import PropTypes from 'prop-types';
import { IMovie } from '../../store/services/dto/movie.interface';
import { Card, Image } from 'antd';
import { Link } from 'react-router-dom';
import _ from 'lodash';
import { useNavigate } from 'react-router-dom';

import './galleryCard.scss';

const { Meta } = Card;

export interface IGalleryCard {
  movieDetail: IMovie;
  type: boolean;
}

const GalleryCard = (props: IGalleryCard) => {
  const { movieDetail: movie } = props;

  const navigate = useNavigate();

  const navigateToDetailPage = () => {
    navigate(`/gallery/${movie.id}`);
  };
  return (
    // <Link to={`/gallery/${movie.id}`}>
    <Card
      hoverable
      style={{ width: 240 }}
      onClick={() => navigateToDetailPage()}
      cover={
        <Image
          alt={movie.title}
          preview={false}
          width={'100%'}
          height={'350px'}
          src={_.isArray(movie.thumb) ? movie.thumb[0] : movie.thumb}
          fallback="data:image/png;base64,"
        />
      }
    >
      <Meta
        title={
          <div>
            <Link to={`/gallery/${movie.id}`}> {movie.title}</Link>
          </div>
        }
      />
    </Card>
    // </Link>
  );
  // <div>{movie.title}</div>;
};

GalleryCard.propTypes = {
  movieDetail: PropTypes.any,
};

export default GalleryCard;
